body {
  margin: 0;
  background-color: #eee;

}
#root{
  text-align: center;
}
.loader {
  position: absolute;
  top:45%;
  left: 45%;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.header{
  margin: 20px auto;
  text-align: left;
}
.numberCards{
  margin: 30px auto;
}
.ncPaper{
  margin: 20px;
  width: 250px;
  height: 120px;
  padding-top: 5px;
}
.ncPaper div.row{
  margin-bottom: -12px;
}
.iconPaper{
  position: relative;
  left: 15px;
  bottom: 25px;
  width: 90px;
  height: 90px;
}
.icon{
  color: #fff;
}
.chartBox{
  width: 80%;
  margin: 30px auto;
}

